import { GitBranch } from '@phosphor-icons/react/dist/ssr'
import { Archivo_Black } from 'next/font/google'
import React from 'react'
import NavigationBar from 'src/components/navigationBar.component'
import { cn } from 'src/lib/utils'

const font = Archivo_Black({
	variable: '--font-sans',
	weight: '400',
})

type indeedStatus = 'constructive' | 'cautionary' | 'destructive'
const logotypeName = 'Alterworld'
const logInfoPrompt = '[alw:info] '
const commandlineEntries: {
	isPrompt?: boolean
	status?: indeedStatus
	log?: string
}[] = [
	{ log: `alternate --commit $this` },
	{ status: 'cautionary', log: `make pattern --destructive` },
	{ log: `sv & > .backup` },
	{ isPrompt: true, log: `$ > ` },
]

const HomePage: React.FC<Readonly<{}>> = () => (
	<>
		<header className=".hero-bonner"></header>
		<main className="w-full">
			<div className="max-w-fit m-auto">
				<div>
					<h1
						className={cn(
							`${font.className} inline-block before:content-['${logotypeName}'] before:text-primary before:absolute before:ml-0.5 before:-z-10 text-4xl sm:text-6xl bg-logotype-texture bg-contain bg-clip-text text-transparent`
						)}
					>
						{logotypeName}
					</h1>
					<span className="bg-constructive ml-2 inline-flex items-center">
						[<GitBranch />] master/
					</span>
				</div>
				<hr />
				<div className="inline-flex gap-x-4 w-full">
					<p>workspace</p>
					<ul className="marker:text-constructive list-disc list-inside bg-card/50 backdrop-blur-sm rounded-b-md w-full mt-0.5 px-2 divide-y-2">
						{commandlineEntries.map((commantlineEntry, index) => (
							<li
								key={index}
								className={`marker:text-${commantlineEntry.isPrompt ? 'accent' : commantlineEntry.status}`}
							>
								{!commantlineEntry.isPrompt && logInfoPrompt}
								{commantlineEntry.log}
								{commantlineEntry.isPrompt && <span className="bg-destructive">_</span>}
							</li>
						))}
					</ul>
				</div>
			</div>
		</main>
		<footer className="w-full">
			<NavigationBar />
		</footer>
	</>
)

export default HomePage
